import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			showReturn:false,//返回到那？
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			orderTotalPrice:0,//总价格
			goodsId:0,//商品id
			goodsSkuId:0,//规则
			token:'',
			address:{},//地址
			orderId:null,//订单ID
			freight:null,//运费
			quantityNumber:'',//商品数量
			commodityPrice:'',//商品价格
			addressId:'',//收货地址id 
			cartId:''
		};
	},
	async onLoad(options){
		let self = this;
		self.showReturn = false;
		self.orderId = options.id;
		self.cartId = options.cart_id;
		self.loadData();
		//获取地址
		// self.getAdressDetail();
		//获取token
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				console.log(res.data);
				self.token = res.data.token;
			}
		});
		
	},
	onShow() { //返回显示页面状态函数
		//，来进行局部刷新
		this.loadData();
		this.showReturn = false;
	},
	onUnload() {   
		if(this.showReturn == true){
			wx.navigateTo({   
				url:'/pages/order/order?state=1&tabIndex=8'
			}) 
		}
		
	},
	
	computed:{
		...mapState(['hasLogin'])
	},
	methods: {
		//请求数据
		async loadData(){
			let self = this;
			self.$axios(self.$baseUrl.getListSpay,{
				cart_id:self.cartId
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					self.cartList =  result.goods_list; //订单
					self.address = result.address;//地址
					self.freight = result.express_price;//运费
					self.orderTotalPrice = result.order_pay_price;//总价
					self.quantityNumber = result.order_total_num;//购买商品数量
					self.commodityPrice = result.order_total_price;//商品价格
					if(result.address !== null){
						self.addressId = result.address.address_id;
					}
					
					
				}
				
			},(error) =>{
				console.log(error)
			})
			let list = await this.$api.json('cartList'); 
			let cartList = list.map(item=>{
				item.checked = true;
				return item;
			});
			
			// this.calcTotal();  //计算总价
		},
		onShow() { //返回显示页面状态函数
				//，来进行局部刷新
				 let pages = getCurrentPages();
		         let currPage = pages[pages.length-1];
		         if (currPage.data.addressId !=""){
			        //更换地址
			        this.getAdressDetail(currPage.data.addressId );
		         }
		
			},
		//更换地址
		getAdressDetail(id){
			let self = this;
			self.$axios(self.$baseUrl.getAdressDetail,{
				id:id
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					self.address =  result.detail;//地址
					self.addressId = result.detail.address_id;
					
				}else if(res.data.code == 0){
					uni.showToast({title:'请选择收货地址',icon:'success'});
				}
				
				
			},(error) =>{
				console.log(error)
			})
		
			
		},
		
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		//结算
		createOrder(){
			let self = this;
			if(!self.addressId){
				uni.showToast({title:'请选择地址！',icon:'none'});
				return;
			}
			self.showReturn = true;
			self.$axios(self.$baseUrl.cartPay,{
				address_id:self.addressId,
				cart_id:self.cartId
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					 // 发起微信支付
					wx.requestPayment({
					  'timeStamp': result.timestamp,
					  'nonceStr': result.nonceStr,
					  'package': result.package,
					  'signType': result.signType,
					  'paySign': result.paySign,
					  success: function (res) {
						  // 跳转到付款完成展示界面
						  uni.navigateTo({
						  	url:'/pages/order/order?state=2&tabIndex=8'
						  })
					  },
					  fail: function (res) {
						App.showError('订单未支付', function () {
						  // 跳转到未付款订单展示界面
						  uni.navigateTo({
						  	url:'/pages/order/order?state=1&tabIndex=8'
						  })
						});
					  },
					  cancel: function (res) {
					  	  uni.navigateTo({
					  		url:'/pages/order/order?state=1&tabIndex=8'
					  	})
					  }
					});
					
				}else if(res.data.code == 0){
					uni.showToast({title:'很抱歉，商品库存不足',icon:'none'});
					self.showReturn = false;
					uni.navigateTo({
						url:'/pages/index/index'
					})
				}
				
				
			},(error) =>{
				console.log(error)
			})
		},
		//更改地址
		changeAddress(){
			uni.navigateTo({
				url: `/pages/user/address/address`
			})
		},
	     
		//商品详情页面
		commodityDetails(id){
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		}
			
	}
}
